.wrapper-modal {
  position: fixed;
  inset: 0px;
  background-color: rgb(00 0 /71%);
  display: flex;
  justify-content: center;
}
.presentation {
  z-index: 1200;
  position: absolute;
}
.modal-close {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
  z-index: 1000;
  color: white;
}
.modal {
  position: relative;
  max-width: 800px;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14),
    0px 1px 14px 0px rgba(0, 0, 0, 0.12);
  background: #111;
  overflow: hidden;
  border-radius: 8px;
  transition: all 400ms ease-in-out 2s;
  animation: fadeIn 400ms;
  height: 50em;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.modal_poster-img {
  width: 100%;
  height: auto;
}
.modal_contents {
  padding: 40px;
  color: white;
  /* background-color: red; */
}
.modal_title {
  padding: 0;
  font-size: 40px;
  margin: 16px 0;
  /* background-color: blue; */
}
.modal_details {
  font-weight: 600;
  font-size: 18px;
}
.modal_overview {
  font-size: 20px;
  line-height: 1.5;
}
/* .modal::-webkit-scrollbar {
  display: none;
  visibility: hidden;
} */
@media screen and (max-height: 768px) {
  .wrapper-modal {
    /* align-items: unset; */
    padding-top: 2rem;
  }
  .modal {
    overflow-y: scroll;
  }
}
@media screen and (max-width: 768px) {
  .modal_overview {
    font-size: 16px;
  }
  .modal_details {
    font-size: 16px;
  }
  .wrapper-modal {
    padding: 0;
  }
  .modal {
    overflow-y: scroll !important;
  }
}
